import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { orgChart } from '@fuse/constant';
import { ToasterService } from '@fuse/services/toaster-service.service';
import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { OrgChartService } from './org-chart.service';

@Component({
  selector: 'app-organization',
  templateUrl: './organization.component.html',
  styleUrls: ['./organization.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class OrganizationComponent implements OnInit{
  data: any;
  topEmployee: any = {
      name: 'Janis Martin',
      designation: 'CEO',
      img: '/assets/images/avatars/Abbott.jpg',
      subordinates: [
          {
              name: 'Matthew Wikes',
              designation: 'VP',
              img: '/assets/images/avatars/Abbott.jpg',
              subordinates: [
                  {
                      name: 'Tina Landry',
                      designation: 'Budget Analyst',
                      img: '/assets/images/avatars/Abbott.jpg',
                      subordinates: []
                  }

              ]
          },
          {
              name: 'Patricia Lyons',
              designation: 'VP',
              img: '/assets/images/avatars/Abbott.jpg',
              subordinates: [
                  {
                      name: 'Dylan Wilson',
                      designation: 'Web Manager',
                      img: '/assets/images/avatars/Abbott.jpg',
                      subordinates: []
                  },
                  {
                      name: 'Deb Curtis',
                      designation: 'Art Director',
                      img: '/assets/images/avatars/Abbott.jpg',
                      subordinates: []
                  }
              ]
          },
          {
              name: 'Larry Phung',
              designation: 'VP',
              img: '/assets/images/avatars/Abbott.jpg',
              subordinates: []
          }
      ]
  };
  constructor(
    private apiloader: ApiLoaderService,
    private toast: ToasterService,
    private orgService: OrgChartService) { }

  ngOnInit() {     
    this.apiloader.setLoader(true);
    this.orgService.getData('organization-chart').subscribe(response => {
        if (response.data){
            this.apiloader.setLoader(false);
            const chartData = this.convertArray(response.data);
            this.data = chartData[0];
        }
    }, error => {
        this.apiloader.setLoader(false);
        this.toast.showError(error);
    });
   }

  convertArray(data){
    for (let i = 0; i < data.length; i++){
        delete data[i].id;
        delete data[i].department;

        data[i].name = data[i]['first_name'] + ' ' + data[i]['last_name'];
        delete data[i].first_name;
        delete data[i].last_name;

        data[i].img = data[i].profile_image;
        delete data[i].profile_image;
        
        if (data[i].children){
            data[i].subordinates = data[i].children;
            delete data[i].children;
            this.convertArray(data[i]['subordinates']);            
        } else{
            data[i].subordinates = [];
        }
    }
        return data;
  }

}
